
.addRoleContent {
  width: 77.9%;
  margin-left: 20%;
}
.addRoleContent .breadcrumb {
  background: #fff;
  margin-top: 20px;
}
.addRoleContent h3 {
  margin: 0;
  font-weight: bold;
}
.addRoleContent .addPrisonForm {
  border: 1px solid #E6E6E6;
  margin-top: 22px;
}
.addRoleContent .addPrisonForm > div:first-child {
  margin-top: 35px;
  margin-bottom: 10px;
}
.addRoleContent .addPrisonForm > div:nth-child(2) {
  height: 79px;
  border-top: 1px solid #E6E6E6;
}
.addRoleContent .addPrisonForm label {
  height: 35px;
  text-align: center;
  line-height: 35px;
}
.addRoleContent .addPrisonForm .buttonBox > div:first-child button {
  width: 119px;
  height: 34px;
  color: #fff;
  background: #6495ED;
  border: 1px solid #6495ED;
  border-radius: 2px;
  margin-top: 21px;
  margin-right: 10%;
}
.addRoleContent .addPrisonForm .buttonBox > div:nth-child(2) button {
  width: 119px;
  height: 34px;
  color: #666666;
  background: #fff;
  border: 1px solid #E6E6E6;
  border-radius: 2px;
  margin-top: 21px;
}



