/*#addMenu{*/
.editUserContent {
  width: 77.9%;
  margin-left: 20%;
}
.editUserContent .breadcrumb {
  background: #fff;
  margin-top: 20px;
}
.editUserContent > div:first-child {
  height: 62px;
}
.editUserContent h3 {
  margin: 0;
  line-height: 62px;
  font-weight: bold;
}
.editUserContent .editUserForm {
  border: 1px solid #E6E6E6;
}
.editUserContent .editUserForm > div:first-child {
  margin-top: 35px;
  margin-bottom: 10px;
}
.editUserContent .editUserForm > div:nth-child(2) {
  height: 79px;
  border-top: 1px solid #E6E6E6;
}
.editUserContent .editUserForm .check .checkBox {
  position: relative;
  width: 17px;
  height: 17px;
  border: 1px solid #BDBDBD;
  border-radius: 50%;
  margin-top: 11px;
}
.editUserContent .editUserForm .check .checkBox > div {
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -5.5px;
  margin-top: -5.5px;
  width: 11px;
  height: 11px;
  border: 1px solid #EB7C29;
  background: #EB7C29;
  border-radius: 50%;
}
.editUserContent .editUserForm .check p {
  margin-top: 10px;
  margin-left: 5%;
}
.editUserContent .editUserForm label {
  height: 35px;
  text-align: center;
  line-height: 35px;
}
.editUserContent .editUserForm .buttonBox > div:first-child button {
  width: 119px;
  height: 34px;
  color: #fff;
  background: #6495ED;
  border: 1px solid #6495ED;
  border-radius: 2px;
  margin-top: 21px;
  margin-right: 10%;
}
.editUserContent .editUserForm .buttonBox > div:nth-child(2) button {
  width: 119px;
  height: 34px;
  color: #666666;
  background: #fff;
  border: 1px solid #E6E6E6;
  border-radius: 2px;
  margin-top: 21px;
}
