
#leftSide {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  width: 18%;
  overflow: auto;
  /*height:100%;*/
  background: #252932;
}
#leftSide a:hover {
  text-decoration: none;
}
#leftSide .title {
  background: #252932;
}
#leftSide .title a {
  float: left;
  width: 100%;
  color: #fff;
  text-align: center;
  height: 68px;
  line-height: 68px;
  font-size: 17px;
  /*font-weight:bold;*/
  border-bottom: 2px solid #393D49;
}
#leftSide .list {
  background: #252932;
}
#leftSide .list .active {
  background: #6495ED;
  color: #fff;
}
#leftSide .list > a {
  position: relative;
}
#leftSide .list > a span {
  position: absolute;
  right: 20px;
  top: 15px;
}
#leftSide .list a {
  float: left;
  width: 100%;
  color: #9B9B9C;
  text-align: center;
  height: 48px;
  line-height: 45px;
  border-bottom: 2px solid #393D49;
}
#leftSide .list a:hover {
  color: #fff;
}
#leftSide .list ul {
  list-style: none;
  padding: 0;
}



