/*border:1px solid #000;*/
/*}*/
#orderManagement {
  margin-top: 90px;
}
#orderManagement .orderManagementContent {
  width: 77.9%;
  margin-left: 20%;
}
#orderManagement .orderManagementContent .breadcrumb {
  background: #fff;
}
#orderManagement .orderManagementContent > div:nth-child(2) {
  background: #F7F7F7;
  padding: 10px 30px 15px 30px;
  border: 1px solid #E6E6E6;
}
#orderManagement .orderManagementContent > div > div {
  margin-top: 20px;
}
#orderManagement .orderManagementContent .centerLine b {
  margin-left: 32%;
  margin-top: 12px;
}
#orderManagement .orderManagementContent .menuType .form-group > div:first-child label {
  margin-left: 25%;
  margin-top: 8px;
}
#orderManagement .orderManagementContent .searchBox input {
  height: 38px;
}
#orderManagement .orderManagementContent .searchBox .searchBtn {
  background: #6495ED;
  height: 36px;
  border: none;
  color: #fff;
}
#orderManagement .orderManagementContent .buttonBox {
  margin-top: 20px;
  margin-bottom: 20px;
}
#orderManagement .orderManagementContent .buttonBox button {
  width: 107px;
  height: 38px;
  color: #fff;
}
#orderManagement .orderManagementContent .buttonBox button:first-child {
  background: #6495ED;
}
#orderManagement .orderManagementContent .buttonBox button:nth-child(2) {
  background: #E72E4D;
}
#orderManagement .orderManagementContent .orderTable {
  position: relative;
  margin-bottom: 75px;
  margin-top: 30px;
}
#orderManagement .orderManagementContent .orderTable .table {
  margin-bottom: 0;
  color: #929292;
  /*a{*/
  /*&:hover{*/
  /*text-decoration:none;*/
  /*}*/
  /*}*/
}
#orderManagement .orderManagementContent .orderTable .table .textBlue {
  color: #6495ED;
}
#orderManagement .orderManagementContent .orderTable .table .textRed {
  color: #E9445D;
}
#orderManagement .orderManagementContent .orderTable .table thead tr {
  background: #F7F7F7;
}
#orderManagement .orderManagementContent .orderTable .table tbody tr td a:nth-child(2) {
  margin-left: 20%;
}
#orderManagement .orderManagementContent .orderTable .table .selectBox {
  width: 15px;
  height: 15px;
  border: 1px solid #C9C9C9;
  border-radius: 1px;
  margin-left: 25%;
}
#orderManagement .orderManagementContent .orderTable .table .active {
  border-color: #FF5050;
  box-shadow: 0 0 3px 0 #FF5050;
  background: url(../../../static/images/boxActive.png) no-repeat center;
}
#orderManagement .orderManagementContent .orderTable .tableFooter {
  position: absolute;
  bottom: -55px;
  background: #F7F7F7;
  height: 55px;
  border: 1px solid #F4F4F4;
  /*border-top: 0;*/
  -webkit-border-radius-bottomleft: 3px;
  -webkit-border-radius-bottomright: 3px;
}



