/*#setAuthority{*/
.setAuthorityContent {
  width: 77.9%;
  margin-left: 20%;
  margin-top: 20px;
}
.setAuthorityContent .breadcrumb {
  background: #fff;
}
.setAuthorityContent h3 {
  margin: 0;
  font-weight: bold;
}
.setAuthorityContent .selectContent {
  margin-top: 23px;
}
.setAuthorityContent .selectContent > div > div {
  border: 1px solid #E2E2E2;
  margin-bottom: 20px;
  /*height:50px;*/
  padding: 15px 2%;
}
.setAuthorityContent .selectContent > div > div > div:first-child {
  border-bottom: 1px dashed #C6C6C6;
  padding-bottom: 10px;
  margin-bottom: 12px;
}
.setAuthorityContent .selectContent > div > div > div:first-child span {
  margin-top: 6px;
  margin-right: 2%;
  color: #909090;
}
.setAuthorityContent .selectContent > div > div .selectBox {
  width: 15px;
  height: 15px;
  border: 1px solid #C9C9C9;
  border-radius: 1px;
  /*margin-left: 25%;*/
  margin-top: 10px;
}
.setAuthorityContent .selectContent > div > div .active {
  border-color: #FF5050;
  box-shadow: 0 0 3px 0 #FF5050;
  background: url(../../../static/images/boxActive.png) no-repeat center;
}
.setAuthorityContent .buttonBox > div:first-child button {
  width: 119px;
  height: 34px;
  color: #fff;
  background: #6495ED;
  border: 1px solid #6495ED;
  border-radius: 2px;
  margin-top: 21px;
  margin-right: 10%;
}
.setAuthorityContent .buttonBox > div:nth-child(2) button {
  width: 119px;
  height: 34px;
  color: #666666;
  background: #fff;
  border: 1px solid #C6C6C6;
  border-radius: 2px;
  margin-top: 21px;
}
/*}*/



