#commodityData {
  margin-top: 70px;
}
#commodityData .commodityDataContent {
  width: 77.9%;
  margin-left: 20%;
  margin-top: 20px;
}
#commodityData .commodityDataContent .breadcrumb {
  background: #fff;
}
#commodityData .commodityDataContent > div:nth-child(2) {
  background: #F7F7F7;
  padding: 10px 30px 15px 30px;
  border: 1px solid #E6E6E6;
}
#commodityData .commodityDataContent > div > div {
  margin-top: 20px;
}
#commodityData .commodityDataContent .centerLine b {
  margin-left: 32%;
  margin-top: 12px;
}
#commodityData .commodityDataContent .exportButtonBox input {
  color: #fff;
  background: #6096EE;
  border: 1px solid #6096EE;
  border-radius: 2px;
  width: 117px;
  height: 33px;
}



