/**{*/
/*border:1px solid #000;*/
/*}*/
#registrationStatistics {
  margin-top: 70px;
}
#registrationStatistics .registrationStatisticsContent {
  width: 77.9%;
  margin-left: 20%;
  margin-top: 20px;
}
#registrationStatistics .registrationStatisticsContent .breadcrumb {
  background: #fff;
}
#registrationStatistics .registrationStatisticsContent > div:nth-child(2) {
  background: #F7F7F7;
  padding: 10px 30px 15px 30px;
  border: 1px solid #E6E6E6;
}
#registrationStatistics .registrationStatisticsContent > div > div {
  margin-top: 20px;
}
#registrationStatistics .registrationStatisticsContent .centerLine b {
  margin-left: 32%;
  margin-top: 12px;
}
#registrationStatistics .registrationStatisticsContent .menuType .form-group > div:first-child label {
  margin-left: 25%;
  margin-top: 8px;
}
#registrationStatistics .registrationStatisticsContent .searchBox input {
  height: 38px;
}
#registrationStatistics .registrationStatisticsContent .searchBox .searchBtn {
  background: #6495ED;
  height: 36px;
  border: none;
  color: #fff;
}
#registrationStatistics .registrationStatisticsContent .buttonBox {
  margin-top: 20px;
  margin-bottom: 20px;
}
#registrationStatistics .registrationStatisticsContent .buttonBox button {
  width: 107px;
  height: 38px;
  color: #fff;
}
#registrationStatistics .registrationStatisticsContent .buttonBox button:first-child {
  background: #6495ED;
}
#registrationStatistics .registrationStatisticsContent .buttonBox button:nth-child(2) {
  background: #E72E4D;
}
#registrationStatistics .registrationStatisticsContent .balanceTable {
  position: relative;
  margin-bottom: 75px;
  margin-top: 30px;
}
#registrationStatistics .registrationStatisticsContent .balanceTable .table {
  margin-bottom: 0;
  color: #929292;
  /*a{*/
  /*&:hover{*/
  /*text-decoration:none;*/
  /*}*/
  /*}*/
}
#registrationStatistics .registrationStatisticsContent .balanceTable .table .textBlue {
  color: #6495ED;
}
#registrationStatistics .registrationStatisticsContent .balanceTable .table .textRed {
  color: #E9445D;
}
#registrationStatistics .registrationStatisticsContent .balanceTable .table .textOrange {
  color: #DB823D;
}
#registrationStatistics .registrationStatisticsContent .balanceTable .table thead tr {
  background: #F7F7F7;
}
#registrationStatistics .registrationStatisticsContent .balanceTable .table tbody tr td:last-child a {
  /*&:nth-child(2) {*/
  /*margin-left: 20%;*/
  /*}*/
}
#registrationStatistics .registrationStatisticsContent .balanceTable .table tbody tr td:last-child a:first-child {
  margin-right: 20%;
}
#registrationStatistics .registrationStatisticsContent .balanceTable .table tbody tr td:last-child a:nth-child(3) {
  margin-left: 20%;
}
#registrationStatistics .registrationStatisticsContent .balanceTable .table .selectBox {
  width: 15px;
  height: 15px;
  border: 1px solid #C9C9C9;
  border-radius: 1px;
  margin-left: 25%;
}
#registrationStatistics .registrationStatisticsContent .balanceTable .table .active {
  border-color: #FF5050;
  box-shadow: 0 0 3px 0 #FF5050;
  background: url(../../../static/images/boxActive.png) no-repeat center;
}
#registrationStatistics .registrationStatisticsContent .balanceTable .tableFooter {
  position: absolute;
  bottom: -55px;
  background: #F7F7F7;
  height: 55px;
  border: 1px solid #F4F4F4;
  /*border-top: 0;*/
  -webkit-border-radius-bottomleft: 3px;
  -webkit-border-radius-bottomright: 3px;
}



