#topNav {
  position: fixed;
  top: 0;
  z-index: 10;
  width: 82%;
  margin-left: 18%;
  background: #fff;
  /*>div{*/
  /*&:nth-child(2){*/
  /*width:95%;*/
  /*border:1px solid #F0F0F0;*/
  /*}*/
  /*}*/
}
#topNav .breadcrumb {
  background: #fff;
  margin: 11px 0;
  margin-left: 1%;
}
#topNav .photo {
  width: 50px;
  height: 50px;
  border: 1px solid #000;
  border-radius: 50%;
  margin-top: 8px;
  margin-left: 17%;
}
#topNav .user {
  margin-top: 20px;
}
#topNav .user a {
  color: #5A5A5A;
}
#topNav .user a span {
  margin-top: 3px;
  /*margin-left:1%;*/
}
#topNav .user a span:nth-child(2) {
  margin-left: 5%;
}
#topNav .logout a {
  color: #343434;
  margin-top: 23px;
}
#topNav .bottomline {
  width: 95%;
  margin-top: 10px;
  margin-left: 2.4%;
  border-bottom: 2px solid #F0F0F0;
}



